<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.jobconfig .el-table th{
  text-align: center
}


</style>
<template>
  <div class="jobconfig">
   <el-container>
  <el-header style="background-image: url('./static/img/yunjian22.png'); background-size: cover; background-position: top center;">流数据清洗工具</el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)"> <nevigation></nevigation></el-aside>
    <el-main>
      <h3 style="text-align:right">
              <i class="el-icon-info">
              </i>
              任务配置
            </h3>
      <newjob @fresh="getJob()"></newjob>
   <el-row :gutter="10">
    <el-col :span="6"><div class="grid-content" style="text-align:left">
          <i class="el-icon-date" >
        </i> <a>任务列表</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
  <el-row>
    <el-col :span="24">
        <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="300">
    <el-table-column
      fixed
      prop="jobname"
      label="任务名称"
      width="150">
    </el-table-column>
    <el-table-column
      prop="jobdesc"
      label="任务描述"
      width="200">
    </el-table-column>
    <el-table-column
      prop="datatype"
      label="数据源类型"
      width="200">
    </el-table-column>
    <el-table-column
      prop="dataname"
      label="数据源名称"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algtype"
      label="算法类型"
      width="120">
    </el-table-column>
    <el-table-column
      prop="algname"
      label="算法名称"
      width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteconfirm(scope.$index, scope.row,tableData4)"
          type="text"
          size="small">
          移除
        </el-button>
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData4)"
          type="text"
          size="small">
          修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>
    </el-col>
  </el-row>
</el-row>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>
<script>
  import nevigation from "./sub/nevigation.vue";
  import newjob from "./sub/newjob.vue";
  export default {
    components: {
      nevigation,
      newjob,
    },
    data() {
      return {
         tableData4: [],
      }
    },
    created: function () {
      var vm = this;
      vm.getJob();

    },

    methods: {
      getJob(){
         var vm = this;
        vm.tableData4 = [];
         var url = "http://localhost:8080/dataclean/servlet/Job";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
            'data_json': JSON.stringify(data_tmp)
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.tableData4.push({
                  "jobname":Obj[i].jobname,
                  "jobdesc":Obj[i].jobdesc,
                  "datatype":Obj[i].datatype,
                  "dataname":Obj[i].dataname,
                  "algtype":Obj[i].algtype,
                  "algname":Obj[i].algname,
                  
                });
                
              }}
              else{
                console.log("无数据");
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
        deleteconfirm:function(index,row,tableData4){
            var vm=this;
            var r=confirm("请确认是否删除");
            if (r==true)
            {
              vm.deletejob(index,row,tableData4);
            }
            else
            {
              return;
            }
          },
      deletejob(index,row,tableData4){
         var vm = this;
         var url = "http://localhost:8080/dataclean/servlet/deletejob";
        var data_tmp = {
        "jobname":row.jobname,
        };
       $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
            'data_json': JSON.stringify(data_tmp)
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            vm.getJob();
                
      
          },
           error: function (data) {
            console.log(data);
          }
        });
      }

    }

  }

</script>



